
import Vue from 'vue'
import { dialogue, cart } from '@/store'
import { Item, CartItem } from '@/models'
import autoAnimate from '@formkit/auto-animate'
import {
  faCancel,
  faCircleCheck,
  faCircleXmark,
  faPlus,
  faMinus,
} from '@fortawesome/free-solid-svg-icons'

export default Vue.extend({
  data() {
    return {
      item_quantity: 1,
      showQrCode: true,
    }
  },
  computed: {
    item() {
      return dialogue.$dialogue_item
    },
    cart() {
      return cart
    },
    dialogueBox() {
      return dialogue
    },
    faCancel() {
      return faCancel
    },
    faCircleCheck() {
      return faCircleCheck
    },
    faCircleXmark() {
      return faCircleXmark
    },
    faPlus() {
      return faPlus
    },
    faMinus() {
      return faMinus
    },
  },
  methods: {
    closeDialogue(status: boolean) {
      this.dialogueBox.toggleDialogue(status)
    },
    addToCart(item: Item) {
      // this.dialogueBox.$dialogue_box = false
      let data = {
        id: item.id,
        code: item.code,
        suplier: item.suplier,
        description: item.description,
        quantity: this.item_quantity,
      } as CartItem
      this.cart.increment(data)
      this.$toast.success(item.description + 'Added to cart!', {
        duration: 3000,
      })
      this.closeDialogue(false)
    },
    sum() {
      this.item_quantity += 1
      ;(this.$refs.quantity as HTMLInputElement)?.focus()
    },
    sub() {
      this.item_quantity -= 1
      ;(this.$refs.quantity as HTMLInputElement)?.focus()
    },
    count() {},
  },
})
